import { SVG_NS, PADDLESPEED } from '../settings.js';

export default class Paddle {
  constructor(boardHeight, width, height, x, y, up, down) {
    this.boardHeight = boardHeight;
    this.width = width;
    this.height = height;
    this.x = x;
    this.y = y;
    this.speed = PADDLESPEED;
    this.score = 0;

    document.addEventListener("keydown", event => {
      switch (event.key) {
        case up:
          this.up();
          break;
        case down:
          this.down();
          break;
      }
    });

    
  }
getScore(){
  return this.score;
}
incrementScore (){
  this.score = this.score +1;
}
  up() {
    this.y = Math.max(0, this.y - this.speed);
  }

  down() {
    this.y = Math.min(this.boardHeight, this.y + this.speed);
  }
  
  
  
  // //   // variables paddle control
  //  var upPressed = false;
  //  var downPressed = false;
  
  
  // document.addEventListener('keydown', keyDownHandler, false);
  // document.addEventListener('keyup', keyUpHandler, false);
  
  // function keyDownHandler(e) {
  //   if(e.keyCode === KEYS.p1up || e.keyCode === KEYS.p2up) {
  //       upPressed = true;
  //   }
  //   else if(e.keyCode === KEYS.p1down || e.keyCode === KEYS.p2down) {
  //     downPressed = true;
  //   }
  // }
  // function keyUpHandler(e) {
  //   if(e.keyCode === KEYS.p1up || e.keyCode === KEYS.p2up){
  //       upPressed = false;
  //   }
  //   else if(e.keyCode === KEYS.p1down || e.keyCode === KEYS.p2down){
  //     downPressed= false;
  //   }
  // }
  
  
  
  
  // function paddleMove(){
  
  //   if (up{
  //     if (this.y > 0) {
  //       this.y = this.y - this.speed;
  //     }
  //   } if downPressed(){
  //     if (this.y + this.height < this.boardHeight) {
  //       this.y = this.y + this.speed;
  //     }
  //   }
  //   paddleMove();
  // }
  
  //  coordinates(){
  //    let leftX = this.x;
  //    letrightX = this.x + this.width;
  //  }
  
  
  
  
  
  
  render (svg) {
    let paddle = document.createElementNS(SVG_NS, 'rect');
    paddle.setAttributeNS(null, 'x', this.x);
    paddle.setAttributeNS(null, 'y', this.y);
    paddle.setAttributeNS(null, 'width', this.width);
    paddle.setAttributeNS(null, 'height', this.height);
    paddle.setAttributeNS(null, 'fill', 'white');
    paddle.setAttributeNS(null, 'boardGap','');
    paddle.setAttribute(null, 'up', this.up);
    paddle.setAttribute(null, 'down', this.down);
    
    
    
    
    
    svg.appendChild(paddle);
    
    
    
    
    
    
  }
  
}


